* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

@font-face {
    font-family: 'Futura';
    src: local('Futura'), url(/syedsharukhhussainy/static/media/Futura.e07bb855.ttf);
}

.about-container {
    background-color: #5569dc10;
    padding-left: 20rem;
    padding-top: 7rem;
    padding-bottom: 7rem;
    padding-right: 35rem;
}

.about-heading h1 {
    font-family: 'Playfair Display', serif;
    color: #5569dc;
    font-style: italic;
    font-size: 2.2rem;
    font-weight: normal;
}

.about-heading h3 {
    margin-top: 0.5rem;
    font-family: 'Futura';
    color:#5569dc;
    font-weight: lighter;
    letter-spacing: 0.2rem;
    font-size: 1.2rem;
}

.about-para p {
    font-family: 'Futura';
    letter-spacing: 0.1rem;
    line-height: 2;
    font-size: 1.6rem;
    margin-top: 2.3rem;
    color: #40445a;
}
