}

.navbar {
    display: flex;
    list-style-type: none;
    align-items: center;
    height: 100%;
}

.navbar a {
    color: #fff;
    padding-right: 2rem;
    text-decoration: none;
    font-style: italic;
}
.item {
    transition: all 0.2s ease-in-out;
}

.item:hover {
    color: rgba(255, 255, 255, 0.699);
