.main-container {
    font-family: 'Playfair Display', serif;
    display: flex;
}

.main-image {
    background: url(/syedsharukhhussainy/static/media/bg-image.6fa56cb7.jpg);
    width: 75%;
    height: 70vh;
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
}

.image-overlay {
    width: 100%;
    height: 70vh;
    background-color: rgba(26, 33, 71, 0.384);
    color: #fff;
}
.text {
   padding-left: 20rem;
   padding-top: 24rem;
}
.text h3 {
    font-family: 'Oswald', sans-serif;
    font-weight: lighter;
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
}

.text h1 {
    font-family: 'Playfair Display', serif;
    font-weight: bolder;
    font-size: 5rem;
}

.text h4 {
    font-style: italic;
    font-size: 1.5rem;
    font-weight: normal;
    margin-top: 1rem;
}
.user-info {
    background: #5569DC;
    width: 25%;
    height: 70vh;
    display: flex;   
    flex-direction: column;
    align-items: center; 
    color: #fff;
    justify-content: center;
}

.image-container {
    width: 150px;
    height: 150px;
    background: #fff;
    border-radius: 100%;
}

.info-text {
    text-align: center;
